'use client';

import React, { useState } from 'react';
import { processImage, processVideo } from '../lib/objectCounter';

interface ProcessingResults {
  count: number;
  confidence: number;
  precision: number;
  recall: number;
  detectionImage: string;
}

export default function ObjectCountingPage() {
  const [fileType, setFileType] = useState<'image' | 'video'>('image');
  const [file, setFile] = useState<File | null>(null);
  const [currentStep, setCurrentStep] = useState<number>(0);
  const [loading, setLoading] = useState<boolean>(false);
  const [results, setResults] = useState<ProcessingResults | null>(null);
  const [error, setError] = useState<string>('');

  const steps = [
    'File Selection',
    fileType === 'video' ? 'Frame Extraction' : 'Image Processing',
    'Object Detection',
    'Complete'
  ];

  const handleFileTypeChange = (type: 'image' | 'video') => {
    setFileType(type);
    setFile(null);
    setError('');
    setResults(null);
    setCurrentStep(0);
  };

  const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const selectedFile = e.target.files?.[0];
    if (selectedFile) {
      // Validate file type
      const isValidImage = selectedFile.type.startsWith('image/') && fileType === 'image';
      const isValidVideo = selectedFile.type.startsWith('video/') && fileType === 'video';
      
      if (isValidImage || isValidVideo) {
        setFile(selectedFile);
        setError('');
        setCurrentStep(1);
      } else {
        setError(`Please select a valid ${fileType} file.`);
        setFile(null);
        setCurrentStep(0);
      }
    }
  };

  const startProcess = async () => {
    if (!file) {
      setError('Please select a file first.');
      return;
    }

    setLoading(true);
    setError('');
    setResults(null);

    try {
      if (fileType === 'video') {
        // Step 2: Frame Extraction
        setCurrentStep(2);
        await new Promise(resolve => setTimeout(resolve, 1000)); // Visual delay
        
        // Step 3: Object Detection
        setCurrentStep(3);
        const result = await processVideo(file);
        setResults(result);
      } else {
        // Step 2: Image Processing & Object Detection
        setCurrentStep(2);
        await new Promise(resolve => setTimeout(resolve, 500)); // Visual delay
        
        setCurrentStep(3);
        const result = await processImage(file);
        setResults(result);
      }
      
      // Step 4: Complete
      setCurrentStep(4);
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'An unknown error occurred';
      setError(errorMessage);
      setCurrentStep(1); // Reset to file selected step
    } finally {
      setLoading(false);
    }
  };

  const handleDownload = () => {
    if (results && results.detectionImage) {
      const link = document.createElement('a');
      link.href = results.detectionImage;
      link.download = `detection-result-${Date.now()}.png`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  };

  const handleReset = () => {
    setFile(null);
    setFileType('image');
    setCurrentStep(0);
    setLoading(false);
    setResults(null);
    setError('');
    
    // Reset file input
    const fileInput = document.getElementById('file-input') as HTMLInputElement;
    if (fileInput) {
      fileInput.value = '';
    }
  };

  return (
    <div className="min-h-screen bg-background text-foreground">
      <div className="container mx-auto px-4 py-8 max-w-4xl">
        {/* Header */}
        <header className="text-center mb-12">
          <h1 className="text-4xl font-bold mb-4">Object Counting Process</h1>
          <p className="text-muted-foreground text-lg">
            Upload an image or video to count the number of balls using AI detection
          </p>
        </header>

        {/* File Type Selector */}
        <section className="mb-8">
          <h2 className="text-2xl font-semibold mb-4">Select Input Type</h2>
          <div className="flex gap-4 justify-center">
            <button
              onClick={() => handleFileTypeChange('image')}
              className={`px-6 py-3 rounded-lg border-2 transition-all ${
                fileType === 'image'
                  ? 'border-primary bg-primary text-primary-foreground'
                  : 'border-border bg-card hover:border-primary/50'
              }`}
            >
              Image
            </button>
            <button
              onClick={() => handleFileTypeChange('video')}
              className={`px-6 py-3 rounded-lg border-2 transition-all ${
                fileType === 'video'
                  ? 'border-primary bg-primary text-primary-foreground'
                  : 'border-border bg-card hover:border-primary/50'
              }`}
            >
              Video
            </button>
          </div>
        </section>

        {/* File Input */}
        <section className="mb-8">
          <h2 className="text-2xl font-semibold mb-4">Upload File</h2>
          <div className="border-2 border-dashed border-border rounded-lg p-8 text-center">
            <input
              id="file-input"
              type="file"
              accept={fileType === 'image' ? 'image/*' : 'video/*'}
              onChange={handleFileChange}
              className="hidden"
            />
            <label
              htmlFor="file-input"
              className="cursor-pointer block"
            >
              <div className="text-6xl mb-4">üìÅ</div>
              <p className="text-lg mb-2">
                Click to select a {fileType} file
              </p>
              <p className="text-muted-foreground">
                {fileType === 'image' ? 'Supported: JPG, PNG, GIF' : 'Supported: MP4, AVI, MOV'}
              </p>
            </label>
            {file && (
              <div className="mt-4 p-4 bg-secondary rounded-lg">
                <p className="font-medium">Selected: {file.name}</p>
                <p className="text-sm text-muted-foreground">
                  Size: {(file.size / 1024 / 1024).toFixed(2)} MB
                </p>
              </div>
            )}
          </div>
        </section>

        {/* Error Display */}
        {error && (
          <div className="mb-8 p-4 bg-destructive/10 border border-destructive/20 rounded-lg">
            <p className="text-destructive font-medium">Error: {error}</p>
          </div>
        )}

        {/* Start Process Button */}
        <section className="mb-8 text-center">
          <button
            onClick={startProcess}
            disabled={!file || loading}
            className="px-8 py-4 bg-primary text-primary-foreground rounded-lg font-semibold text-lg disabled:opacity-50 disabled:cursor-not-allowed hover:bg-primary/90 transition-colors"
          >
            {loading ? 'Processing...' : 'Start Process'}
          </button>
        </section>

        {/* Progress Stepper */}
        {currentStep > 0 && (
          <section className="mb-8">
            <h2 className="text-2xl font-semibold mb-6">Process Status</h2>
            <div className="stepper">
              {steps.map((step, index) => (
                <div key={index} className="flex flex-col items-center flex-1">
                  <div
                    className={`step ${
                      index + 1 < currentStep
                        ? 'step-complete'
                        : index + 1 === currentStep
                        ? 'step-active'
                        : ''
                    }`}
                  >
                    {index + 1 < currentStep ? '‚úì' : index + 1}
                  </div>
                  <p className="text-sm mt-2 text-center">{step}</p>
                  {index < steps.length - 1 && (
                    <div className="hidden sm:block absolute w-full h-0.5 bg-border top-4 left-1/2 transform -translate-y-1/2 -z-10" />
                  )}
                </div>
              ))}
            </div>
          </section>
        )}

        {/* Loading Spinner */}
        {loading && (
          <section className="mb-8 text-center">
            <div className="spinner mx-auto mb-4"></div>
            <p className="text-muted-foreground">
              {currentStep === 2 && fileType === 'video' ? 'Extracting frames...' :
               currentStep === 3 ? 'Detecting objects...' : 'Processing...'}
            </p>
          </section>
        )}

        {/* Results Display */}
        {results && currentStep === 4 && (
          <section className="mb-8">
            <h2 className="text-2xl font-semibold mb-6">Detection Results</h2>
            <div className="grid md:grid-cols-2 gap-8">
              {/* Statistics */}
              <div className="space-y-4">
                <div className="bg-card border rounded-lg p-6">
                  <h3 className="text-xl font-semibold mb-4">Statistics</h3>
                  <div className="space-y-3">
                    <div className="flex justify-between">
                      <span className="font-medium">Detected Balls:</span>
                      <span className="text-2xl font-bold text-primary">{results.count}</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="font-medium">Confidence Score:</span>
                      <span className="font-semibold">{(results.confidence * 100).toFixed(1)}%</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="font-medium">Precision:</span>
                      <span className="font-semibold">{(results.precision * 100).toFixed(1)}%</span>
                    </div>
                    <div className="flex justify-between">
                      <span className="font-medium">Recall:</span>
                      <span className="font-semibold">{(results.recall * 100).toFixed(1)}%</span>
                    </div>
                  </div>
                </div>
              </div>

              {/* Detection Image */}
              <div className="bg-card border rounded-lg p-6">
                <h3 className="text-xl font-semibold mb-4">Detection Visualization</h3>
                <img
                  src={results.detectionImage}
                  alt="Detection Results"
                  className="w-full rounded-lg border"
                />
              </div>
            </div>
          </section>
        )}

        {/* Action Buttons */}
        {results && (
          <section className="text-center space-x-4">
            <button
              onClick={handleDownload}
              className="px-6 py-3 bg-secondary text-secondary-foreground rounded-lg font-medium hover:bg-secondary/80 transition-colors"
            >
              Download Detection Image
            </button>
            <button
              onClick={handleReset}
              className="px-6 py-3 bg-primary text-primary-foreground rounded-lg font-medium hover:bg-primary/90 transition-colors"
            >
              Count Another File
            </button>
          </section>
        )}
      </div>
    </div>
  );
}
