// Utility functions for simulating object counting process

export async function processImage(file) {
  return new Promise((resolve, reject) => {
    try {
      // Validate file type
      if (!file.type.startsWith('image/')) {
        reject(new Error('Invalid file type. Please select an image file.'));
        return;
      }

      // Simulate processing delay
      setTimeout(() => {
        // Generate simulated results
        const count = Math.floor(Math.random() * 15) + 1; // 1-15 balls
        const confidence = (Math.random() * 0.3 + 0.7).toFixed(2); // 0.70-1.00
        const precision = (Math.random() * 0.2 + 0.8).toFixed(2); // 0.80-1.00
        const recall = (Math.random() * 0.25 + 0.75).toFixed(2); // 0.75-1.00
        
        // Create a simple detection image placeholder (data URL)
        const canvas = document.createElement('canvas');
        canvas.width = 400;
        canvas.height = 300;
        const ctx = canvas.getContext('2d');
        
        // Draw background
        ctx.fillStyle = '#f3f4f6';
        ctx.fillRect(0, 0, 400, 300);
        
        // Draw simulated detection boxes
        ctx.strokeStyle = '#ef4444';
        ctx.lineWidth = 2;
        ctx.fillStyle = '#ef4444';
        ctx.font = '12px Arial';
        
        for (let i = 0; i < count; i++) {
          const x = Math.random() * 320 + 20;
          const y = Math.random() * 220 + 20;
          const size = Math.random() * 30 + 20;
          
          // Draw bounding box
          ctx.strokeRect(x, y, size, size);
          
          // Draw confidence label
          ctx.fillText(`Ball ${(Math.random() * 0.3 + 0.7).toFixed(2)}`, x, y - 5);
        }
        
        const detectionImage = canvas.toDataURL('image/png');
        
        resolve({
          count,
          confidence: parseFloat(confidence),
          precision: parseFloat(precision),
          recall: parseFloat(recall),
          detectionImage
        });
      }, 2000); // 2 second delay
    } catch (error) {
      reject(new Error('Failed to process image: ' + error.message));
    }
  });
}

export async function processVideo(file) {
  return new Promise((resolve, reject) => {
    try {
      // Validate file type
      if (!file.type.startsWith('video/')) {
        reject(new Error('Invalid file type. Please select a video file.'));
        return;
      }

      // Simulate frame extraction delay
      setTimeout(() => {
        // Simulate object detection on extracted frame
        setTimeout(() => {
          // Generate simulated results (similar to image processing)
          const count = Math.floor(Math.random() * 20) + 1; // 1-20 balls for video
          const confidence = (Math.random() * 0.25 + 0.75).toFixed(2); // 0.75-1.00
          const precision = (Math.random() * 0.2 + 0.8).toFixed(2); // 0.80-1.00
          const recall = (Math.random() * 0.25 + 0.75).toFixed(2); // 0.75-1.00
          
          // Create a simple detection image placeholder for video frame
          const canvas = document.createElement('canvas');
          canvas.width = 400;
          canvas.height = 300;
          const ctx = canvas.getContext('2d');
          
          // Draw background (slightly different for video)
          ctx.fillStyle = '#e5e7eb';
          ctx.fillRect(0, 0, 400, 300);
          
          // Add "Video Frame" text
          ctx.fillStyle = '#374151';
          ctx.font = '14px Arial';
          ctx.fillText('Extracted Video Frame', 10, 20);
          
          // Draw simulated detection boxes
          ctx.strokeStyle = '#3b82f6';
          ctx.lineWidth = 2;
          ctx.fillStyle = '#3b82f6';
          ctx.font = '12px Arial';
          
          for (let i = 0; i < count; i++) {
            const x = Math.random() * 320 + 20;
            const y = Math.random() * 200 + 40;
            const size = Math.random() * 25 + 15;
            
            // Draw bounding box
            ctx.strokeRect(x, y, size, size);
            
            // Draw confidence label
            ctx.fillText(`Ball ${(Math.random() * 0.3 + 0.7).toFixed(2)}`, x, y - 5);
          }
          
          const detectionImage = canvas.toDataURL('image/png');
          
          resolve({
            count,
            confidence: parseFloat(confidence),
            precision: parseFloat(precision),
            recall: parseFloat(recall),
            detectionImage
          });
        }, 1500); // Additional 1.5 second delay for object detection
      }, 2500); // 2.5 second delay for frame extraction
    } catch (error) {
      reject(new Error('Failed to process video: ' + error.message));
    }
  });
}
